#visualSet{
	background-color:#d6dde5;
	@include breakpoint($smd){
		// height:200px;
	}
	@include breakpoint($md){ height: 270px; }
	@include breakpoint($lg){ height: 420px; }
	@include breakpoint(992px 1276px){ height: 320px; }
	position: relative;
	overflow: hidden;
	.ctrl{
		position: absolute; 
		z-index: 1;
		bottom: 30px;
		left: 50%;
		margin-left: -570px;
		@include breakpoint($sm){ margin-left: 0px; left: 2%; }
		@include breakpoint($md){ margin-left: -360px; }
		@include breakpoint($lg){ margin-left: -570px; }
		@include breakpoint(992px 1276px){ margin-left: -470px; }
		button{
			color:#fff;
			font-size: 24px;
			width: 50px;
			height: 50px;
			line-height: 50px;
			background-color: $btnbgcolor;
			margin-right: -3px;
		}
	}
	.visualSlide{
		.items{
			@include breakpoint($sm){
				.bg{
					display:block;
					background-position:-100px 0;
					height:200px;
					background-size:auto 100%;
					background-repeat:no-repeat;
				}
			}
			@include breakpoint($md){
				display: inline-block;
				float: left;
				
				width: 100%;
				height:320px;
				@include breakpoint($md){
					height: 420px;
				}
				a{
					display: inline-block;
					height: 420px;
					width: 100%;
				}
				.bg{ 
					display: block;
					width:100%;
					background-repeat: no-repeat;
					height:320px;
					@include breakpoint($md){
						height:300px;
						background-position: 50% 0;
					}
					@include breakpoint($lg){	width: 100%;height: 100%;	}
					@include breakpoint($md){ background-size: auto 100%; }
					@include breakpoint(992px 1276px){ background-size: auto 320px; }
				}
			}
		}
	}
}

