			}
			@include breakpoint($lg){
				width: calc(25% - 19px);
			}

		}
		li.line2{ 
			width:calc(100% - 19px);
			@include breakpoint($lg){width:calc(50% - 19px);}
		}
		li a{
			display:block;
			height:40px;
			border:1px solid $bdcolor;
			position:relative;
			line-height:38px;
			padding-left:17px;
			padding-right:20px;
			letter-spacing:-1px;
			&:before {
				content: '\f054';
				display:inline;
				font-family:'FontAwesome';
				position:absolute;
				right:19px;
				top:0;
				font-size:9px;
			}
			&:hover,
			&:focus {color: $sitecolor;}
			@include breakpoint($sm){
				font-size:15px;
				padding:0 0 0 10px;
				text-align:center;
				&:before {display:none; }
			}
		}
	}
	
